import { Component, OnInit } from '@angular/core';
import {Pdf,ServicioService}from './../../services/servicio.service';
import tarjeta from'./../../../assets/json/tarjetas.json';
import { ActivatedRoute, Router } from '@angular/router';


@Component({
  selector: 'app-gaseoso',
  templateUrl: './gaseoso.page.html',
  styleUrls: ['./gaseoso.page.scss'],
})
export class GaseosoPage implements OnInit {
  Tarjetas :any;t2=false;
  public card:string;
  tarjeta; ejemplo=false; show= false;ejemplo2=false;ejemplo3=false;
  public texto:string;
  personajesArr = ['crispi', 'cori','cesia','carmel','cristal']; 
  aux= ["", "","","", "", "", "", "", ""];
  section="";
  personajeSeleccionado:any;
  personajeSeleccionadoaux:string;
  personajeSeleccionadoaux2:string;
  p:any;
  objaux:Pdf;
  idea;idea2;idea3;idea4="";
  pdf:Pdf={
    id :'',
    num:'',
    nombre:'',
    ambiente:'',
    factor:'',
    personaje:'',
    solitario:true,
    solido:'',
    gaseoso:'',
    liquido:'',
    P1:'',
    P2:'',
    P3_1:'',
    P3_2:'',
    P3_3:'',
    P3_4:'',
    tarjeta:'',
    tarjeta2:'',
    idea1:'',
    idea2:'',
    idea3:'',
    idea4:'',
    ideaselecionada:'',
    aprendizaje:'',
    V:'',
    C:'',
    R:'',
    P:'',
    tipo:'',
    img:''
  };
  constructor(private serv:ServicioService,
    private route: ActivatedRoute,
    private router:Router) { }
  avanzar(section){
    this.section = section;
  }
  verificar(){
    this.show = !this.show;
    this.ejemplo2 = !this.ejemplo2;
  }
async ngOnInit() {
   this.card='';
    this.section="videopersonaje";
    this.mipromesa();
    this.route.params.subscribe(params => { 
      const _personaje= params.personajeSeleccionado;
      const DEFAULT_SELECT='';
      const NUM_SELECT={
        0:this.personajesArr[0],
        1:this.personajesArr[2],
        2:this.personajesArr[1],
        3:this.personajesArr[3],
        4:this.personajesArr[4]
      }
      const number = NUM_SELECT[_personaje] || DEFAULT_SELECT;
      this.personajeSeleccionadoaux=number.toUpperCase();
      this.personajeSeleccionadoaux2=number;
      this.p=this.personajeSeleccionadoaux;
      this.Tarjetas= tarjeta;
      for(var i = 0 ;i<this.aux.length; i++)  {  
        this.aux[i]=this.Tarjetas.tarjeta[_personaje].tarjetas[i+1];
     }
     this.aux.sort(function() { return Math.random() -  0.4});
    });

  }
  async getInfo(){
    await this.serv.localFirebase(this.pdf.id,this.pdf.num);
  }
    cambiadorTarjeta(){
      this.t2= !this.t2;
    }
    seleccionadorTarjeta(tarjeta){
      if(this.t2==false){
        this.pdf.tarjeta= this.aux[tarjeta];
        this.texto =this.pdf.P2;
        this.card=this.pdf.tarjeta;
        
      }
      else{
        this.pdf.tarjeta2= this.aux[tarjeta];
        this.card=this.pdf.tarjeta2;
      }
    }
  openPopUP(item){
    switch(item){
      case 'tarjeta':
        this.tarjeta=(!this.tarjeta);
          break;
      case 'ejemplo':
        this.ejemplo = (!this.ejemplo);
          break;
      default:
          break;
    }
    this.show = !this.show;
  }
  openPopUP2(){
        this.ejemplo2 = (!this.ejemplo2);
        if(this.ejemplo2==false){
            this.ejemplo3= !this.ejemplo3;
        }    
  }
  openPopUP3(){
    this.ejemplo3= !this.ejemplo3;
    this.show = !this.show;    
}
 async ideasaved(){
  this.pdf.idea1=this.idea;
  this.pdf.idea2=this.idea2;
  this.pdf.idea3=this.idea3;
  this.pdf.idea4=this.idea4;
  this.pdf.gaseoso="Terminado";
  this.serv.addItem(this.pdf);
  this.serv.AddItem(this.pdf);
 }
 async mipromesa(){
  new Promise((resolve) => {
    this.objaux = JSON.parse(this.serv.getItem('_cap_pdf2'));
     this.pdf=  this.objaux; 
     return resolve(this.pdf);
 });
  }
  alLector(){
    this.router.navigateByUrl('scanner');
  }
  gotoSolido(){
    this.router.navigateByUrl('/solido/pasoG/0');
  }

}
